<template>
  <div class="login_bg container-fluid vh-100">
    <div class="row justify-content-center">
      <div class="col-md-8 col-lg-5">
        <div class="bg-dark-opacity vh-100 p-5">
          <h2 class="text-white mt-12 mb-5">管理者登入</h2>
          <v-form ref="form" class="text-white mb-4"
            v-slot="{ errors }" @submit="loginSubmit">
            <div class="mb-3">
              <label for="adminEmail" class="form-label">Email</label>
              <v-field
                id="adminEmail"
                name="adminEmail"
                type="email"
                class="form-control"
                :class="{ 'is-invalid': errors['adminEmail'] }"
                placeholder="請輸入 Email"
                rules="required|email"
                v-model="admin.username"
              ></v-field>
              <error-message
                name="adminEmail"
                class="invalid-feedback"
              ></error-message>
            </div>
            <div class="mb-5">
              <label for="adminPassword" class="form-label">密碼</label>
              <v-field
                id="adminPassword"
                name="adminPassword"
                type="password"
                class="form-control"
                :class="{ 'is-invalid': errors['adminPassword'] }"
                placeholder="請輸入密碼"
                rules="required"
                v-model="admin.password"
              ></v-field>
              <error-message
                name="adminPassword"
                class="invalid-feedback"
              ></error-message>
            </div>
            <button :disabled="Object.keys(errors).length > 0"
              type="submit"
              class="btn btn-primary w-100"
            >登入</button>
          </v-form>
          <router-link to="/" class="d-block text-white text-center">前往前台首頁</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      admin: {
        username: '',
        password: '',
      },
    };
  },
  methods: {
    loginSubmit() {
      this.$http.post(`${process.env.VUE_APP_API_BASEURL}/admin/signin`, this.admin).then((res) => {
        const { token, expired } = res.data;
        document.cookie = `pfgToken=${token};expires=${new Date(expired)};`;
        this.$swal({
          icon: 'success',
          title: '登入成功',
          showCloseButton: true,
        });
        this.$router.push('/admin/product');
      }).catch((err) => {
        this.$swal({
          icon: 'error',
          title: err.response.data.message,
          showCloseButton: true,
        });
      });
    },
  },
};
</script>
